/**** REACT: SEASONS ****/
.season-display {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 50vh;
}

#root {
  position: relative;
}
.winter {
  background-color: aliceblue;
}

.summer {
    background-color: orange;
}

.icon-left {
  position: absolute;
  left:10px;
  top:10px;
}

.icon-right {
  position: absolute;
  right:10px;
  bottom: 10px;
}

.ui.dimmer{
  position:relative;
  top:auto;
  left:auto;
  padding:5em 1em
}

.ui.loader p{
  margin-top:70px
}